:root{--primary-color: #d19a66;--secondary-color: #d19a60;--background-color: #000000;--text-color-primary: #ffffff;--text-color-secondary: #bbbbbb}.homeContainer{position:relative;padding-bottom:150px;min-height:100vh;background-color:var(--background-color);color:var(--text-color-primary)}.footer{position:relative;z-index:1;margin-top:50px}.chatbot{position:fixed;bottom:1rem;right:1rem;z-index:1000;max-height:400px;width:auto;max-width:calc(100% - 2rem);box-shadow:0px 0px 10px rgba(0,0,0,.5);border-radius:8px;transition:width .3s ease-in-out}.chatbot.open{width:calc(100% - 2rem)}.heroWithImageCentered{display:flex;justify-content:center;align-items:center;margin-bottom:20px}.heroSectionContainer{text-align:center;background-color:#000;color:#fff;padding:20px;border-radius:10px}.profileImage{width:150px;height:150px;border-radius:50%;object-fit:cover;margin-bottom:20px;box-shadow:0 4px 20px rgba(0,0,0,.5)}.resumeLinkContainer{text-align:center;margin:20px 0}.viewModeContainer{text-align:center;margin:20px 20px}.viewModeButton{padding:10px 20px;font-size:1.2rem;cursor:pointer;margin:0 10px;background-color:var(--primary-color);color:#fff;border:none;border-radius:5px;transition:background-color .3s}.viewModeButton:hover{background-color:var(--secondary-color)}.viewModeButton.active{background-color:var(--secondary-color)}.resumeButton{padding:10px 20px;font-size:1.2rem;cursor:pointer;background-color:var(--primary-color);color:#fff;border:none;border-radius:5px;transition:background-color .3s}.resumeButton:hover{background-color:var(--secondary-color)}.resumePopup{position:fixed;top:0;left:0;width:100%;height:100%;background:rgba(0,0,0,.5);display:flex;justify-content:center;align-items:center;z-index:2000}.resumePopupContent{background:#222;padding:20px;border-radius:10px;max-width:80%;max-height:80%;width:600px;position:relative}.closeButton{position:absolute;top:10px;right:10px;background:var(--primary-color);color:#fff;border:none;border-radius:5px;cursor:pointer;padding:5px 10px}.resumeIframe{width:100%;height:500px;border:none}.introSection{text-align:center;margin-bottom:4rem}.introSection h2{font-size:2.5rem;color:var(--primary-color);margin-bottom:1rem}.introSection p{font-size:1.2rem;color:var(--text-color-secondary)}.skillsSection{text-align:center}.skillsSection h2{font-size:2rem;color:var(--primary-color);margin-bottom:2rem}.skillsGrid{display:grid;grid-template-columns:repeat(auto-fit, minmax(150px, 1fr));gap:1.5rem;margin-top:2rem}.skillCard{background:#222;border:1px solid #444;padding:1rem;box-shadow:0 4px 8px rgba(0,0,0,.5);transition:transform .3s;color:var(--text-color-primary)}.skillCard:hover{transform:translateY(-5px)}.socialLinksContainer{display:flex;justify-content:center;margin-top:20px}.socialLink{margin:0 10px;display:inline-block}.socialLogo{width:40px;height:40px;transition:transform .3s ease}.socialLogo:hover{transform:scale(1.1)}